.App {
  text-align: center;
}

.App-bar {
  background-color: #001d00 !important;
  width: 100% !important;
}

.App-divider {
  border-color: rgb(76, 136, 86) !important;
}

.App-center-box {
  position: relative !important;
  min-height: 350px;
  border-color: rgb(76, 136, 86) !important;
  border: 2px solid;
  border-radius: 15px !important;
  background: rgb(0, 84, 0) !important;
  background: linear-gradient(
    90deg,
    rgba(0, 47, 0, 1) 9%,
    rgba(0, 29, 0, 1) 47%,
    rgba(0, 47, 0, 1) 82%
  ) !important;
  opacity: 0.9;
  color: white !important;
}

.App-logo {
  height: 50px;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Banana-peel {
  max-width: 542px;
  width: 95%;
}

.Wallet-button-div-bottom {
  justify-content: center;
  width: 92%;
  position: absolute;
  bottom: 25px;
}

.Wallet-button-div-middle {
  width: 100%;
  justify-content: center;
  position: relative;
  margin-top: 145px;
}

.Connect-wallet-button {
  display: inline-flex !important;
  background: rgb(131, 58, 180);
  background: linear-gradient(
    90deg,
    rgba(131, 58, 180, 1) 0%,
    rgba(253, 29, 29, 1) 50%,
    rgba(252, 176, 69, 1) 100%
  ) !important;
}

.Connect-wallet-button:hover {
  background: rgb(131, 58, 180) !important;
}

.Dancing-banana {
  margin-left: 20px;
  width: 300px;
}

.Loan-list-table {
  color: white !important;
}

.Open-loan-modal {
  background-color: #003500 !important;
}

.Open-loan-button {
  background-color: yellow !important;
  color: black !important;
}

/* small screen overrides */
/* anything smaller than a tablet screen of 768px */
@media only screen and (max-width: 768px) {
  .Connect-wallet-button {
    height: 40px !important;
    line-height: 40px !important;
    margin-top: 5px !important;
    font-size: 12px !important;
    zoom: 100% !important;
  }

  .SolUSD-price {
    font-size: 12px !important;
    margin-top: 15px !important;
  }

  .Dancing-banana {
    width: 80%;
  }

  .Loan-list-outter {
    margin-bottom: 80px;
  }

  .Loan-list-cell-value {
    font-weight: bold !important;
  }
}

/* anything smaller than a mobile M screen of 375px */
@media only screen and (max-width: 374px) {
  .Connect-wallet-button {
    margin-top: 8px !important;
    zoom: 95% !important;
    font-size: 10px !important;
  }
  .SolUSD-price {
    text-align: right !important;
    margin-right: 5px !important;
  }

  .Updating-loan-list-text {
    font-size: 1rem !important;
  }
}

/* anything smaller than a mobile M screen of 375px */
@media only screen and (max-width: 500px) {
  .Open-loan-modal {
    width: 70% !important;
  }
}
